import React from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Home from "../pages/Home";
import Checkout from "../pages/Checkout";
import NotFound from "../pages/NotFound";
import Search from "../pages/Search";
import { GlobalStorage } from "../context/GlobalContenxt";
import MainHeader from "../components/MainHeader";

const MainRoutes = () => {
  return (
    <BrowserRouter>
      <GlobalStorage>
        <MainHeader />
        <Routes>
          <Route path="pokecommerce/" element={<Home />} />
          <Route path="pokecommerce/checkout" element={<Checkout />} />
          <Route path="pokecommerce/search/:id" element={<Search />} />
          <Route path="pokecommerce/*" element={<NotFound />} />
        </Routes>
      </GlobalStorage>
    </BrowserRouter>
  );
};

export default MainRoutes;
